.wp-block-dekode-api-fnugg{position:relative}.wp-block-dekode-api-fnugg-card__title{position:absolute;top:0;left:0;width:100%;margin:0 !important;padding:1rem;border-top-left-radius:5px;border-top-right-radius:5px;background:#323232;color:#fffefe !important}.wp-block-dekode-api-fnugg-card__overlay__sub__title{position:absolute;bottom:46%;width:100%;background:rgba(32,32,32,0.5) !important;text-align:center}.wp-block-dekode-api-fnugg-card__overlay__sub__title>h4{margin:0 !important;color:#fffefe !important}.wp-block-dekode-api-fnugg-card__overlay__sub__title>p{margin:0 !important;color:#fffefe !important}.wp-block-dekode-api-fnugg-card--grid{display:grid;grid-template-columns:repeat(2, 1fr);grid-template-rows:repeat(2, 0.5fr);gap:1px 1px;grid-template-areas:"cloud degree" "wind description";text-align:center;border-bottom:3px solid #40A3BE}.cloud{grid-area:cloud;padding:2rem 0}.cloud>img{object-fit:contain;width:100px;height:100px}.cloud>h5{margin:0 !important}.degree{grid-area:degree}.degree>h1{color:#ff0000 !important}.wind{grid-area:wind;padding:2rem 0}.wind__row__1{display:flex;flex-direction:row;flex-wrap:wrap}.wind__row__2{display:flex;flex-direction:row;flex-wrap:wrap}.wind__row__1>img{margin-top:10px;margin-left:4rem;width:50px;height:50px}.wind__row__1>h3{margin:0 !important;margin-left:1rem !important;font-weight:normal !important}.wind__row__1>h5{margin:14px !important;font-weight:normal !important}.wind>p{margin:0 !important}.description{grid-area:description;display:flex;flex-direction:row;justify-content:center;margin:1.5rem 0;padding:1rem 0}.description>img{width:50px;height:50px}.description>p{margin:8px !important}#wp-block-dekode-api-fnugg__resort-name{margin:5rem !important}
